interface ResearchSectionProps {
  title: string;
  description: string[];
  readMoreLinks: Array<{ href: string; text: string }>;
  imageUrl?: string;
  reverse?: boolean;
}

export default function ResearchSection({
  title,
  description,
  readMoreLinks,
  imageUrl,
  reverse = false
}: ResearchSectionProps) {
  return (
    <div style={{
      display: 'grid',
      gridTemplateColumns: 'repeat(auto-fit, minmax(400px, 1fr))',
      gap: '48px',
      alignItems: 'start',
      marginBottom: '48px',
      flexDirection: reverse ? 'row-reverse' : 'row'
    }}>
      {/* Image */}
      <div style={{
        display: 'flex',
        justifyContent: 'center',
        order: reverse ? 2 : 1
      }}>
        <div style={{
          width: '100%',
          height: '400px',
          borderRadius: '16px',
          background: imageUrl ? 'transparent' : 'linear-gradient(135deg, #fff7ed 0%, #fed7aa 100%)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          border: imageUrl ? 'none' : '2px solid #f97316',
          boxShadow: imageUrl ? 'none' : '0 8px 30px rgba(0, 0, 0, 0.1)',
          overflow: 'hidden'
        }}>
          {imageUrl ? (
            <img 
              src={imageUrl} 
              alt={title}
              style={{
                width: '100%',
                height: '100%',
                objectFit: 'contain',
                borderRadius: '0'
              }}
              onError={(e) => {
                // Fallback to placeholder if image fails to load
                const target = e.target as HTMLImageElement;
                target.style.display = 'none';
                const parent = target.parentElement;
                if (parent) {
                  parent.style.background = 'linear-gradient(135deg, #fff7ed 0%, #fed7aa 100%)';
                  parent.style.border = '2px solid #f97316';
                  parent.style.boxShadow = '0 8px 30px rgba(0, 0, 0, 0.1)';
                  parent.innerHTML = `
                    <div style="text-align: center; color: #f97316;">
                      <div style="font-size: 4rem; margin-bottom: 16px;">ðŸ”¬</div>
                      <p style="font-size: 1.125rem; font-weight: 600;">Research Image</p>
                      <p style="font-size: 0.875rem; opacity: 0.8;">Image not available</p>
                    </div>
                  `;
                }
              }}
            />
          ) : (
            <div style={{
              textAlign: 'center',
              color: '#f97316'
            }}>
              <div style={{ fontSize: '4rem', marginBottom: '16px' }}>ðŸ”¬</div>
              <p style={{ fontSize: '1.125rem', fontWeight: 600 }}>Research Image</p>
              <p style={{ fontSize: '0.875rem', opacity: 0.8 }}>No image available</p>
            </div>
          )}
        </div>
      </div>

      {/* Text Content */}
      <div style={{ order: reverse ? 1 : 2 }}>
        {/* Title */}
        <h2 style={{
          fontSize: '2rem',
          fontWeight: 700,
          color: '#1f2937',
          marginBottom: '20px',
          lineHeight: 1.2,
          marginTop: 0
        }}>
          {title}
        </h2>

        {/* Description */}
        {description.map((paragraph, index) => (
          <p key={index} style={{
            fontSize: '1rem',
            color: '#6b7280',
            lineHeight: 1.6,
            marginBottom: '20px'
          }}>
            {paragraph}
          </p>
        ))}

        {/* Read More Section */}
        <div style={{
          background: '#f8fafc',
          borderRadius: '12px',
          padding: '20px',
          border: '1px solid #e2e8f0',
          margin: 0
        }}>
          <h3 style={{
            fontSize: '1.25rem',
            fontWeight: 600,
            color: '#1f2937',
            marginBottom: '12px'
          }}>
            Read More
          </h3>

          <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
            {readMoreLinks.map((link, index) => (
              <a key={index} href={link.href} style={{
                color: '#f97316',
                textDecoration: 'none',
                fontSize: '0.875rem',
                fontWeight: 500,
                transition: 'color 0.3s ease',
                cursor: 'pointer'
              }}
              onMouseEnter={(e) => {
                (e.target as HTMLElement).style.color = '#ea580c';
                (e.target as HTMLElement).style.textDecoration = 'underline';
              }}
              onMouseLeave={(e) => {
                (e.target as HTMLElement).style.color = '#f97316';
                (e.target as HTMLElement).style.textDecoration = 'none';
              }}>
                â†’ {link.text}
              </a>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}